}

.courses-list {
  list-style-type: none;
  padding: 0;
}

.all-courses-div {
  width: 80%;
  margin: auto auto 1rem auto;
  border-radius: 12px;
  height: 260px;
}

.all-courses-p {
  width: 80%;
}

.courses-img {
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}

