  background: #f15223;
  color: #ffff;
  font-size: 14px;
}

.all-courses-category-list:hover,
.all-courses-link:hover {
  color: var(--primary-color);
  cursor: pointer;
}

.all-courses-category-list {
	color: black;
}

.all-courses-link {
  text-decoration: none;
  color: #000000;
}

.menu-icon {
  float: right;
}

.all-categories {
  display: none;
}

.all-categories-active {
  opacity: 1;
  z-index: 2;
  transition-timing-function: ease-in-out;
}

.courses-tutor-image-radius {
  position: absolute;
  bottom: 30px;
  right: 0;
  border: 2px solid var(--primary-color);
  padding: 3px;
}

.special-loader {
	display: flex;
	justify-content: center;
}
.special-loader img {
	height: 250px;
	width: 250px;
}
@media only screen and (min-width: 600px) {
  .all-courses-div {
    width: 45%;
  }
}

@media only screen and (min-width: 768px) {
  .all-courses-div {
    width: 45%;
  }

  .all-categories {
    display: block;
  }

  .menu-icon {
    display: none;
  }
}

@media only screen and (min-width: 992px) {
  .all-courses-div {
    width: 30%;
  }
}

